.recipie-container{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(220px,1fr));
    grid-gap: 1.5em; 
}

.recipie-card{
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0 5px 15px var(--shadow-color);
    .recipie-card-info{
        padding: 15px;
    }
    .custom-image{
        z-index: -1;
    }
    .author-img{
        height: 60px;
        width: 60px;
        border-radius: 50%;
        border: 3px solid var(--background-light);
        box-shadow: 0 1px 3px var(--shadow-color);
        margin-top: -50px;
        z-index: 2
    }
    .recipie-title{
        font-size: 1.25em;
        font-weight: bold;
        margin-bottom: 0.5em;
        color: var(--text-color);
        margin-top: 0.25em;
    }
    .recipie-desc{
        color: var(--text-light);
        font-size: .9em;
        letter-spacing: 1px;
    }
    .view-btn{
        font-size: 1em;
        font-weight: bold;
        color: var(--primary-color);
        margin-top: 0.75em;
        display: inline-block;
    }
}